<template>
    <div>
        <highcharts :constructorType="'stockChart'" class="hc" 
        :options="chartOptions" 
        ref="chart"></highcharts>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            chartOptions: {
                title: {
                    text: 'Nasdaq Composite Index History'
                },
                legend: {
                    enabled: true
                },
                plotOptions: {
                    series: {
                        showInLegend: true
                    }
                },
                series: []
            }
        }
    },
    created() {
        this.fillData();
    },
    methods: {
        async fillData() {
            let ohlc = [];
            const myApi = 'b922393424a8774196e1ec9237c001bf';
            const urlBase = `https://financialmodelingprep.com/api/v3/historical-price-full/index/%5EIXIC?apikey=${myApi}`; 
            await axios
            .get(urlBase)
            .then((res) => {
                const returnedData = res.data["historical"]; // object:key is date
                for(const key in returnedData) {
                    let singleohlc = [];
                    // console.log(returnedData[key]['date']);
                    let date = (new Date(returnedData[key]['date'])).getTime();
                    let open = parseFloat(returnedData[key]['open']);
                    let high = parseFloat(returnedData[key]['high']);
                    let low = parseFloat(returnedData[key]['low']);
                    let close = parseFloat(returnedData[key]['close']);
                    singleohlc.push(date, open, high, low, close);
                    // console.log(singleohlc);
                    ohlc.push(singleohlc);
                }
            })
            .catch((err) => {
                console.log(err);
            })
            ohlc.reverse();
            // volume.reverse();
            this.chartOptions.series = [{
                type: 'candlestick',
                id: 'mainSeries',
                name: 'Nasdaq Composite',
                data: ohlc,
                yAxis: 0
            }, {
                type: 'sma',
                linkedTo: 'mainSeries',
                params: {
                    period: 200
                },
                yAxis: 0
            }, {
                type: 'sma',
                linkedTo: 'mainSeries',
                params: {
                    period: 900
                },
                yAxis: 0
            }, 
            // {
            //     type: 'column',
            //     name: 'Volume',
            //     data: volume,
            //     yAxis: 1
            // }
            ];
            // console.log(typeof this.chartOptions.series);
        }
    }
};
</script>

