<template>
    <div>
        <el-table
         :data="tableData"
         border
         style="width: 100%">
        <el-table-column
        prop="name"
        label="Name"
        width="200">
        </el-table-column>
        <el-table-column
        prop="time"
        label="Time"
        width="200">
        </el-table-column>
        <el-table-column
        prop="price"
        label="Price"
        width="150">
        </el-table-column>
        <el-table-column
        prop="note"
        label="Note">
        </el-table-column>       
        </el-table>
    </div>
</template>


<script>
import axios from "axios";


// npm run serve
export default {
    data() {
        return {
            tableData: []
        }
    },
    created() {
        this.getNasdaq();
        // this.getoilprice();
        this.getgoldprice();
        this.getsp500();
        this.treasuryNoteData();
        this.UnemploymentRate();
        this.FEDRate();
        this.getHeadLines();
    },
    methods: {
        // free trial expired
        async getoilprice() {
            const url = 'https://api.oilpriceapi.com/v1/prices/latest/?by_code=WTI_USD';
            const headers = {
                'Authorization': 'Token a6f349547ff2aab2f5d034152957eef2',
                'Content-Type': 'application/json'
            };
            await axios
            .get(url, {headers})
            .then((res) => {
                let price = res.data.data["price"];
                let name = "WTI_OIL";
                let time = res.data.data["created_at"];
                let oil = {name, time, price};
                this.tableData.push(oil);
            })
            .catch((err) => {
                console.log(err);
            })
        },
        async getgoldprice() {
            const myApi = 'tw2albymedt36y44nd3b23jr9g80xog5vejivtnejw4s4jv2xn2zr98k6oo6';
            const goldURL = `https://metals-api.com/api/latest?access_key=${myApi}&base=USD&symbol=XAU`;
            await axios
            .get(goldURL)
            .then((res) => {
                let time = res.data['date'];
                let currentgoldprice = parseFloat(res.data['rates']['XAU']);
                let price = (1 / currentgoldprice).toFixed(2);
                let name = "Gold";
                let gold = {name, time, price};
                this.tableData.push(gold);
            })
            .catch((err) => {
                console.log(err);
            })
        },
        async getsp500() {
            let name = "S&P 500";
            let time = 0;
            let price = 0.0;
            let note = "";
            const myApi = 'b922393424a8774196e1ec9237c001bf';
            const urlBase = `https://financialmodelingprep.com/api/v3/historical-price-full/index/^GSPC?apikey=${myApi}`; 
            await axios
            .get(urlBase)
            .then((res) => {
                const returnedData = res.data["historical"];
                let count = 0;
                for(const key in returnedData) {
                    count += 1;
                    let date = returnedData[key]['date'];
                    let sp500now = parseFloat(returnedData[key]['close']);
                    if(count === 1) {
                        time = date;
                        price = sp500now.toFixed(2);
                    }
                    // if(returnedData[key]['date'] === '2020-12-31') {
                    //     let firstDay = parseFloat(returnedData[key]['close']);
                    //     let ytd = ((price - firstDay) / firstDay) * 100;
                    //     note = "S&P500 YTD: " + ytd.toFixed(2).toString() + " %";
                    //     break;
                    // }
                }
                let sp500Sum = {name, time, price, note};
                this.tableData.push(sp500Sum);
            })
            .catch((err) => {
                console.log(err);
            })
        },
        async getNasdaq() {
            let name = "Nasdaq Composite Index";
            let time = 0;
            let price = 0;
            let note = "";
            let nasdaqNow = 0.0;
            // let nasdaqytd = 0.0;
            const myApi = 'b922393424a8774196e1ec9237c001bf';
            const urlBase = `https://financialmodelingprep.com/api/v3/historical-price-full/index/^IXIC?apikey=${myApi}`; 
            await axios
            .get(urlBase)
            .then((res) => {
                const returnedData = res.data["historical"];
                let count = 0;
                for(const key in returnedData) {
                    count += 1;
                    let date = returnedData[key]['date'];
                    nasdaqNow = parseFloat(returnedData[key]['close']);
                    if(count === 1) {
                        time = date;
                        price = nasdaqNow.toFixed(2);
                    }
                    // if(returnedData[key]['date'] === '2020-12-31') {
                    //     let firstDay = parseFloat(returnedData[key]['close']);
                    //     nasdaqytd = ((price - firstDay) / firstDay) * 100;
                    //     // console.log(ytd);
                    //     note = "Nasdaq Composite YTD: " + nasdaqytd.toFixed(2).toString() + " %";
                    //     break;
                    // }
                }
                let nasadaqSum = {name, time, price, note};
                this.tableData.push(nasadaqSum);
            })
            .catch((err) => {
                console.log(err);
            })
        },
        async treasuryNoteData() {
            let name = "U.S. 10 Year Treasury Note";
            let time = 0;
            let price = 0;
            const myApi = 'LWWEAVVU5LH8YH6Y';
            const urlBase = `https://www.alphavantage.co/query?function=TREASURY_YIELD&interval=daily&maturity=10year&apikey=${myApi}`;
            await axios
            .get(urlBase)
            .then((res) => {
                const returnedData = res.data["data"];
                let count = 0;
                for(const key in returnedData) {
                    count += 1;
                    let date = returnedData[key]['date'];
                    let rate = returnedData[key]['value'];
                    if(count === 1) {
                        time = date;
                        price = rate.toString() + " %";
                        break;
                    }
                }
                let tenyearnote = {name, time, price};
                this.tableData.push(tenyearnote);
            })
            .catch((err) => {
                console.log(err);
            })            
        },

        async UnemploymentRate() {
            let name = "US unemployment rate";
            let time = 0;
            let price = 0;
            const myApi = 'LWWEAVVU5LH8YH6Y';
            const UERURL = `https://www.alphavantage.co/query?function=UNEMPLOYMENT&apikey=${myApi}`;
            await axios
            .get(UERURL)
            .then((res) => {
                const returnedData = res.data['data'];
                let count = 0;
                for(const key in returnedData) {
                    count += 1;
                    let date = returnedData[key]['date'];
                    let rate = returnedData[key]['value'];
                    if(count === 1) {
                        time = date;
                        price = rate.toString() + " %";
                        break;
                    }
                }
                let uer = {name, time, price};
                this.tableData.push(uer);
            })
            .catch((err) => {
                console.log(err);
            })
        },

        async FEDRate() {
            let name = "Fed Funds Rate";
            let time = 0;
            let price = 0;
            const myApi = 'LWWEAVVU5LH8YH6Y';
            const FEDRateURL = `https://www.alphavantage.co/query?function=FEDERAL_FUNDS_RATE&interval=monthly&apikey=${myApi}`;
            await axios
            .get(FEDRateURL)
            .then((res) => {
                const returnedData = res.data['data'];
                let count = 0;
                for(const key in returnedData) {
                    count += 1;
                    let date = returnedData[key]['date'];
                    let rate = returnedData[key]['value'];
                    if(count === 1) {
                        time = date;
                        price = rate.toString() + " %";
                        break;
                    }
                }
                let fedRateSum = {name, time, price};
                this.tableData.push(fedRateSum);
            })
            .catch((err) => {
                console.log(err);
            })
        },
        // newsapi.org, newsdata.io, api.currentsapi --> failed 
        async getHeadLines() {
            let name = "News about stock";
            let today = new Date();
            let time = today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate();
            let price = "_";
            let note = "";
            let hl = [];
            const MyNewsAPI = 'eba2f90f70f9ee0f8bfe4c23703724bd';
            const NewsAPIURL = `https://gnews.io/api/v4/search?q=stock&lang=en&token=${MyNewsAPI}`;
            await axios
            .get(NewsAPIURL)
            .then((res) => {
                // this.data.headlines = res.data["articles"]["title"];
                const articles = res.data["articles"];
                let count = 0;
                for(const key in articles) {
                    count += 1;
                    hl.push(articles[key]["title"]);
                    if(count === 5) {
                        break;
                    }
                }
                note = hl.toString();
                let newsSum = {name, time, price, note};
                this.tableData.push(newsSum);
            })
            .catch((err) => {
                console.log(err);
            })
        }
    }
}
</script>


